$btn-blue: rgba(187, 187, 226, 0.589);
$dark: black;
$light: white;
@mixin shadowBox($dark: black, $light: white) {
  border-top: 1px solid rgba($dark, 0.1);
  border-left: 1px solid rgba($dark, 0.1);
  border-bottom: 1px solid rgba($dark, 0.5);
  border-right: 1px solid rgba($dark, 0.5);
  box-shadow: 3px 3px 4px rgba($dark, 0.5),
    -1px -1px 1px rgba(255, 255, 255, 0.2);
}
@mixin shadowBoxInset($dark: black, $light: white) {
  //shadowBox와는 반대로
  border-bottom: 1px solid rgba($dark, 0.1);
  border-right: 1px solid rgba($dark, 0.1);
  border-top: 1px solid rgba($dark, 0.5);
  border-left: 1px solid rgba($dark, 0.5);
  box-shadow: inset 3px 3px 5px rgba($dark, 0.6),
    inset -3px -3px 5px rgb(white, 0.8);
}
h1 {
  text-align: center;
}

.btn-container {
  width: 50px;
  height: 50px;
  button {
    // .btn-container button
    width: 100%;
    height: 100%;
    // outline: 1px solid $dark;
    // outline-offset: 3px;
    @include shadowBox(black, white);
    &:hover {
      //button:hover
      &:active {
        background-color: rgba(231, 228, 228, 0.5);
        @include shadowBoxInset(
          rgba(10, 8, 8, 0.575),
          rgba(255, 255, 255, 0.897)
        );
      }
    }
  }
}
