@font-face {
  font-family: "Digital-7 Regular";
  font-weight: 200;
  src: url("./fonts/digital-7.ttf");
}

.calculator {
  display: block;
  width: fit-content;
  background-color: rgba(132, 131, 148, 0.6);
  padding: 20px;
  margin: 50px auto;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.3),
    -5px -5px 30px rgba(255, 255, 255, 0.9);
  position: relative;
  .tas {
    position: relative;
    background-color: rgb(202, 138, 138);
    width: 225px;
    margin-right: 0;
    div {
      margin: 0;
      padding: 0;
      &:first-child {
        textarea {
          font-family: "Digital-7 Regular";
          font-size: 18px;
        }
      }
      textarea {
        font-family: "Digital-7 Regular";
        font-size: 25px;
        display: block;
        width: 100%;
        height: 100%;
        margin: 0;
        border-style: none;
        resize: none;
        border-radius: 0px;
      }
    }
  }
  .btns {
    top: 10px;
    position: relative;
  }
}
.num {
  font-family: "Digital-7 Regular";
  font-size: 25px;
}
.control {
  font-size: 18px;
  background-color: rgba(226, 140, 140, 0.3);
}
.selfOperator {
  font-size: 18px;
  background-color: rgba(139, 129, 201, 0.3);
}
.operator {
  font-size: 18px;
  background-color: rgba(250, 209, 76, 0.3);
}
