.nav-dropdown{position:relative}.nav-dropdown .nav-title{width:400px;background-color:aqua;margin:0;padding-left:20px}.nav-dropdown .nav-title::after{content:"☝";font-size:17px;display:inline-block;margin-left:20px;transition:transform 700ms;padding:10px}.nav-dropdown .nav-list{max-height:0px;overflow:hidden;list-style:square;width:360px;background-color:bisque;margin:0;position:absolute;transition:max-height 900ms}.nav-dropdown:hover .nav-list{max-height:300px}.nav-dropdown:hover .nav-title::after{transform:rotateX(180deg) translateY(-5px)}